<template>
	<div>
		<nav role="navigation" class="navbar navbar-default  navbar-static-top">
			<!-- Brand and toggle get grouped for better mobile display -->
			<div class="navbar-header">
				<button type="button" data-target="#navbarCollapse" data-toggle="collapse" class="navbar-toggle">
                <span class="sr-only">Toggle navigation</span>
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>
            </button>
				<router-link to="/" class="navbar-brand"><img alt="Brand" src="../assets/GlobalCob-RPT.gif"></router-link>
			</div>
			<!-- Collection of nav links and other content for toggling -->
			<div id="navbarCollapse" class="collapse navbar-collapse">
				<ul class="nav navbar-nav">
					<li class="dropdown">
						<a href="#" class="dropdown-toggle" data-submenu="" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">Cadastro <span class="caret"></span></a>
						<ul class="dropdown-menu">
							<li class="dropdown-submenu">
								<a tabindex="0">Cliente</a>
								<ul class="dropdown-menu">
									<li><router-link to="/">Buscar</router-link></li>						
									<li><router-link to="/juridico">Buscar (Somentes contratos do jurídico)</router-link></li>							
								</ul>
							</li>
						</ul>
					</li>
				</ul>
				<ul class="nav navbar-nav navbar-right">
					<li>
						<router-link to="/" class="nav-item nav-link"><span @click="logout" class="glyphicon glyphicon-log-in" aria-hidden="true"> Sair</span></li>
				</ul>
			</div>
		</nav>
	</div>
</template>

<script>
  export default {

        created:function(){
            this.getStart();
        },   

  methods : {
        logout () {		
          let url = "http://192.168.8.183:8081/backend/logout";
          this.$http.get(url).then((res) => {
            localStorage.setItem('loggedIn', 'false');
            localStorage.setItem('username', '');
            location.reload();
          });
        }, 
				
				getStart(){
                $(document).ready(function(){								
                   $('[data-submenu]').submenupicker();
                });
        }
	}



  }
</script>


