<script>



  export default { 
     name: 'cliente',
        data () {
          return {
            // msg : 'cliente',
            contratos : ''
          }
        },        
        created:function(){
            this.getStart();
            this.getContratos;

        },
        methods:{
            getStart(){
                $(document).ready(function(){
                    $('.select').select();

                     $('#clienteTable').DataTable({
                          "bSort"  : false,
                          "lengthMenu"   : [[3, 5, 10,-1],[3,5,10,"Todos"]],
                          "stateSave": true,
                          "language": {
                                "url":  'http://cdn.datatables.net/plug-ins/1.10.13/i18n/Portuguese-Brasil.json'
                            }

                     });
                });
            },

           getContratos(){
               this.$http.get("http://localhost:8081/backend/contratos").then((res) => {
                   this.contratos = res.body._embedded.contratos.reverse();
                   console.log(this.contratos);
               }, (err) => {
                   console.log(err);
               });
           }


        }
  }
</script>


